<script lang="ts">import { onMount } from "svelte";
import { SIZES } from "lib/config";
;
import { getRainbowHSL } from "lib/colors";
export let grid = [];
export let sizeIndex = 0;
let canvas;
let ctx;
$: cellSizeStr = SIZES[sizeIndex].cell;
$: gridSize = SIZES[sizeIndex].grid;
$: cellSize = Number(cellSizeStr.replace("rem", "")) * 16;
$: if (ctx && grid) {
    draw(ctx, grid);
}
const offset = (n) => n * cellSize + cellSize / 2;
function draw(ctx, grid) {
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    grid.forEach((row, y) => {
        row.forEach((isAlive, x) => {
            const color = isAlive ? getRainbowHSL(y, x, gridSize) : "#222";
            ctx.fillStyle = color;
            ctx.beginPath();
            ctx.arc(offset(x), offset(y), cellSize / 2 - cellSize * 0.125, 0, 2 * Math.PI, false);
            ctx.fill();
        });
    });
}
$: if (canvas && gridSize && cellSize) {
    const length = gridSize * cellSize;
    if (length && length !== canvas.height) {
        canvas.height = length;
        canvas.width = length;
        draw(ctx, grid);
    }
}
onMount(() => {
    ctx = canvas.getContext("2d");
});
</script>

<style>
  canvas {
    border: solid 0.25rem #222;
    border-radius: 0.75rem;
    padding: 0.125rem;
    margin: 1.75rem auto;
  }</style>

 <canvas bind:this={canvas} /> 
